<template>
  <div class="home">
    <div id="theater">
      <video id="video1" src="../assets/video/video1.mp4" controls="false" hidden playsinline webkit-playsinline></video>
      <video id="video2" src="../assets/video/video2.mp4" controls="false" hidden playsinline webkit-playsinline></video>
      <video id="video3" src="../assets/video/video3.mp4" controls="false" hidden playsinline webkit-playsinline></video>
      <div class="position-relative">
        <div class="notice d-none position-absolute">
          <button @click="play">play</button>
          <button @click="play2">play2</button>
        </div>
        <div class="notice2 position-absolute">
          <button @click="plaa">開始</button>
        </div>
        <canvas id="canvas"></canvas>
      </div>
      <br />
    </div>
  </div>
</template>

<script>
export default {
  name: 'home',
  data () {
    return {
      canvas: null,
      width: 0,
      height: 0,
      canvasInterval: null
    }
  },
  mounted () {
    var canvas = document.getElementById('canvas')
    var ctx = canvas.getContext('2d')
    var videoall = document.querySelectorAll('video')
    console.log(videoall)
    // set canvas size = video size when known
    videoall.forEach(video => {
      video.addEventListener('loadedmetadata', function () {
        canvas.width = video.videoWidth
        canvas.height = video.videoHeight
      })
      video.addEventListener('play', function () {
        // var $this = this;
        (function loop () {
          if (!video.paused && !video.ended) {
            document.querySelector('.notice').classList.add('d-none')
            ctx.drawImage(video, 0, 0)
            setTimeout(loop, 1000 / 30) // drawing at 30fps
          }
        })()
      }, 0)
      video.addEventListener('pause', function () {
        (function loop () {
          // ctx.drawImage(img, 0, 0)
          document.querySelector('.notice').classList.remove('d-none')
          // document.querySelector('.notice').remove.classList('d-none')
          console.log('pause')
        })()
      }, 0)
    })
  },
  methods: {
    play () {
      document.querySelector('#video1').pause()
      document.querySelector('#video3').pause()
      document.querySelector('#video2').play()
    },
    play2 () {
      document.querySelector('#video2').pause()
      document.querySelector('#video3').pause()
      document.querySelector('#video1').play()
    },
    plaa () {
      document.querySelector('#video1').play()
      document.querySelector('.notice2').classList.add('d-none')
    }
  }
}
</script>

<style lang="scss">
  .home{
    // background: red;
    .notice, .notice2{
      top: 50%;
      left: 50%;
    }
  }
  video{
    // display: none;
  }
</style>



